@import 'common/mixins';
@import 'common/colors';
@import 'common/animations';

body {
  font-family: var(--font-body-family);

  &.no-scroll {
    overflow: hidden;
  }
}

h1,
h2,
h3,
h4,
h5,
.h0,
.h1,
.h2,
.h3,
.h4,
.h5 {
  letter-spacing: 0.025rem;
  margin-bottom: 2rem;
  font-family: var(--font-heading-family);
}

.button,
.button-label,
.customer button,
.shopify-challenge__button {
  font-weight: 600;
  @include transition_default();

  &.button__underline {
    background: none;
    padding: 0;
    border-radius: 0;
    box-shadow: none;
    display: block;
    margin-top: 3rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid currentColor;
    font-weight: bold;
    font-size: 1.3rem;
    color: currentColor;
    min-height: auto;
    position: relative;

    .icon--arrow {
      position: absolute;
      top: 2px;
      right: 2px;
      @include transition_default();
    }

    &:hover {
      box-shadow: none;

      .icon--arrow {
        right: -2px;
      }
    }
  }

  .rte & {
    @include transition_default();
  }
}

.nomargin {
  margin: 0;
}

.nomargin-top {
  margin-top: 0;
}

.nomargin-bottom {
  margin-bottom: 0;
}

.bg-body {
  background: rgb(var(--color-background));
}

.icon--arrow {
  display: block;
  width: 5px;
  height: 5px;
  border-top: 1px solid rgb(var(--color-foreground));
  border-left: 1px solid rgb(var(--color-foreground));
  margin-right: 23px;
  margin-top: 8px;

  &:after {
    content: '';
    display: block;
    width: 1px;
    height: 10px;
    background-color: rgb(var(--color-foreground));
    left: 0;
    top: 0;
  }

  &:after{
    transform: rotate(-45deg) translate(3px, 0);
  }

  &.down_arrow {
    transform: rotate(-135deg);
  }

  &.right_arrow {
    transform: rotate(135deg);
  }
}

.announcement-bar {
  border-bottom: none;

  &.color-pro {
    background: var(--brand-medium-grey);
  }
}

.shopify-section-header-sticky {
  z-index: 2;
  @include transition_default();
}

.header {
  padding-top: 0;
  padding-bottom: 0;

  @include media-query($max,$lg) {
    justify-content: space-between;

    > * {
      flex-basis: 0;
      flex-grow: 1;

      &.header__icons {
        width: auto !important;
        justify-content: flex-end;
      }
    }
  }
}

.header__icon .icon,
.modal__close-button .icon {
  @include transition_default();
}

.header__inline-menu {

  .list-menu__item {
    font-family: var(--font-body-family-bold);
  }

  .header__submenu .header__menu-item {
    padding-left: 0;
    padding-right: 0;
  }

  .list-menu__item.header__menu-item--top {
    @include transition_default();
    font-family: var(--font-body-family-bold);
    text-transform: uppercase;

    .link {
      @include transition_default();
    }
  }

  .list-menu:hover .list-menu__item:not(:hover) {
    opacity: 0.58;
  }

  details[open] > .header__menu-item {
    text-decoration: none;
  }
}

.megamenu {

  .page-width {
    max-width: 1450px;
  }

  ul,
  li {
    padding-left: 0;
    margin-bottom: 0;
  }
}

.megamenu--text_column {

  .link.header__menu-item--megamenu__sub {
    font-size: 1.6rem;
    text-transform: uppercase;
  }

  ul {
    list-style: none;
  }
}

.megamenu--image_column {

  a {
    text-decoration: none;
    text-transform: uppercase;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  .megamenu--caption {
    margin-top: 1.6rem;
    color: rgb(var(--color-foreground));
    font-size: 2rem;
  }
}

.breadcrumbs {
  list-style: none;

  .breadcrumbs__list {
    padding-left: 0;
  }

  .breadcrumbs__item {
    display: inline-block;

    &:not(:last-child)::after {
      content: '/';
      display: inline-block;
      margin-left: 0.5rem;
      margin-right: 0.5rem;
    }
  }

  .breadcrumbs__link {

    &.breadcrumbs__link--home {

      span {
        position: absolute;
        overflow: hidden;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        border: 0;
        clip: rect(0 0 0 0);
        word-wrap: normal;
      }

      svg {
        height: 12px;
        margin-top: 5px;
      }
    }
  }

  .breadcrumbs__item:not(:last-child):after {
    margin-left: 0.5rem;
  }
}

.card,
.article-card {

  .card--flags {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 1;

    .card--flag {
      background: rgb(var(--color-background));
      text-transform: uppercase;
      font-size: 1rem;
      font-weight: 700;
      padding: 0 10px;
      border-radius: 2px;

      svg {
        width: 12px;
        display: inline-block;
        vertical-align: middle;
        margin-right: 4px;
      }
    }
  }
}

.article-card .card--flags .card--flag {
  font-size: 1.4rem;
  padding: 5px 15px;
}

.footer-block-image {

  img {
    margin-bottom: 1rem;
  }
}

.footer-block__details-content {
  line-height: 1.6rem;
}

.footer-block--menu {

  > ul {

    > li > .list-menu__item--link {
      color: rgb(var(--color-foreground));
    }

    ul li .list-menu__item--link {
      color: rgb(var(--brand-offwhite));
    }
  }
}

.footer-block{

  &.grid__item.footer-block--menu {

    @media screen and (max-width: 749px) {
      text-align: center;

      &:not(:last-child) {
        margin-top: 0;
        margin-bottom: 0;
      }

      .footer-block__details-content {
        margin-bottom: 0;
      }
    }

    .list-menu__item {
      display: block;
    }
  }

  &.footer-block--newsletter {
    align-items: flex-start;
    margin-top: 0;

    .footer-block__newsletter {
      text-align: left;

      > *:first-child {
        margin-top: 0;
      }
    }

    #mc_embed_signup {
      background: none;

      form {
        padding: 0;
      }

      .mc-field-group {
        min-height: auto;
        margin-bottom: 1rem;
        width: 100%;
        padding-bottom: 0;

        label {
          position: absolute;
          color: rgb(var(--color-background));
          top: 50%;
          transform: translateY(-50%);
          padding: 0 1rem;
          font-size: 1rem;
          z-index: 1;
        }

        input {
          background: rgb(var(--color-foreground));
          border: none;
          border-radius: 0;
          position: relative;

          &:focus {
            z-index: 2;
          }
        }

        #mce-responses {
          position: absolute;
          top: 100%;
          padding: 0;
          width: 100%;
          margin: 0;

          div.response {
            width: 100%;
            margin: 0;
          }
        }

        div.mce_inline_error {
          position: absolute;
          left: 0;
          top: 100%;
        }
      }

      #mc-embedded-subscribe {
        background: rgba(var(--color-background),0.5);
        border: none;
        position: absolute;
        right: 0;
        bottom: 0;
        min-height: 0;
        box-shadow: none;
        border-radius: 0;
        padding: 0 1rem;
        margin: 0 0 1rem 0;
        min-width: auto;
        z-index: 2;

        @media screen and (min-width: 750px) and (max-width: 989px) {
          position: relative;
          width: 100%;
          background: var(--brand-offwhite);
          right: auto;
          bottom: auto;
        }
      }
    }
  }

  .footer__list-social.list-social:only-child {

    @media screen and (min-width: 750px) {
      justify-content: flex-start;
    }

    .list-social__item .link--text {
      color: rgb(var(--brand-offwhite));
    }
  }
}

.media {

  &.media--about {
    padding-bottom: 55%;
  }

  &.collection-hero__image-container {
    padding-bottom: 0;
    height: 100%;
    min-height: 300px;
    margin-left: -1.5rem;
    margin-right: -1.5rem;

    @media screen and (min-width: 750px) {
      margin-left: -7rem;
      margin-right: 0;
    }
  }

  &.media--offset {
    margin-top: 2rem;
    margin-bottom: 2rem;

    @media screen and (min-width: 990px) {
      margin-top: -2rem;
      margin-bottom: 0;
    }
  }
}

.card__inner .media {

  img {
    object-fit: contain;
  }
}

.product-grid {
  margin-top: 0;
}

.card-information__text {
  margin-bottom: 0;
  font-size: 2.2rem;
  padding-right: 0;
}

.card-wrapper:hover {

  .card-information {

    .card-information__text {
      text-decoration: none;
    }
  }
}

.card-information__type {
  display: block;
  text-transform: uppercase;
}

.information__variant-wrapper {
  position: relative;
  overflow: hidden;
  padding-top: 1rem;
  padding-bottom: 1rem;
  min-height: 5rem;
}

@media screen and (min-width: 990px) {
  .grid__item-text {

    > .rte {
      padding-right: 5rem;
    }
  }
}

.rte {

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {

    &:first-child {
      margin-top: 0;
    }
  }
}

.product--video_wrapper {
  border-radius: 2px;
  overflow: hidden;

  .media > .deferred-media__poster,
  .media > iframe {
    position: absolute;
  }

  .media > iframe {
    width: 100%;
    height: 100%;
  }
}

.slider {

  @media screen and (min-width: 990px) {
    &.slider--mobile:not(.slider--desktop) .slider__slide.cloned,
    &.slider--tablet:not(.slider--desktop) .slider__slide.cloned {
      display: none;
    }
  }

  @media screen and (min-width: 750px) and (max-width: 989px) {
    &.slider--mobile:not(.slider--tablet) .slider__slide.cloned,
    &.slider--desktop:not(.slider--tablet) .slider__slide.cloned {
      display: none;
    }
  }

  @media screen and (max-width: 749px) {
    &.slider--tablet:not(.slider--mobile) .slider__slide.cloned,
    &.slider--desktop:not(.slider--mobile) .slider__slide.cloned {
      display: none;
    }
  }
}

.section--slider {

  .slider {
    margin-top: 0;

    &.slider--desktop {
      @media screen and (min-width: 990px) {
        scroll-padding-left: 0;

        .slider__slide {

          &.grid__item {
            width: 100%;
            padding-left: 0;
          }
        }
      }
    }

    &.slider--tablet {
      @media screen and (min-width: 750px) and (max-width: 989px) {
        scroll-padding-left: 0;

        .slider__slide {

          &.grid__item {
            width: 100%;
            padding-left: 0;
          }
        }
      }
    }

    &.slider--mobile {
      @media screen and (max-width: 749px) {
        scroll-padding-left: 0;

        .slider__slide {

          &.grid__item {
            width: 100%;
            padding-left: 0;
          }
        }
      }
    }
  }

  .grid__item.slider__slide {
    padding-left: 0;

    a.product__media {

      > img {
        transform: scale(1);
        @include transition_default();
      }

      &:hover > img {
        transform: scale(1.02);
      }
    }

    .product__media {
      color: white;
      text-decoration: none;

      @media screen and (max-width: 749px) {
        padding-bottom: 100% !important;
      }

      .slide_text {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        &.justify-content--left {
          justify-content: flex-start;
        }

        &.justify-content--right {
          justify-content: flex-end;
        }

        &.align-items--top {
          align-items: flex-start;
        }

        &.align-items--bottom {
          align-items: flex-end;
        }

        .slide_text--align {
          max-width: 33.33%;
          flex-grow: 1;
          flex-shrink: 0;
          padding: 4rem;

          @media screen and (min-width: 750px) and (max-width: 989px) {
            max-width: 60%;
          }

          @media screen and (max-width: 749px) {
            max-width: 90%;
          }
        }

        h3 {
          font-family: var(--font-body-family);
          text-transform: uppercase;
        }

        h2, h3 {
          margin-top: 0;
        }
      }
    }
  }

  .slider-buttons {
    justify-content: space-between;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin-top: -22px;

    @media screen and (max-width: 749px) {
      display: none;
    }

    .slider-button {
      background: rgb(var(--color-background));
      color: rgb(var(--color-foreground));

      .icon {
        height: 0.8rem;
      }
    }

    .slider-counter {
      display: none;
    }
  }
}

.video-section {
  position: relative;

  .video-section--title {
    position: absolute;
    width: 100%;
    max-width: 600px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: white;
    z-index: 4;
  }
}

.video-section--wrapper {
  position: relative;
  padding-bottom: 56.25%;

  deferred-media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-bottom: 0;
    z-index: 2;

    .deferred-media__poster {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .media {
      padding-bottom: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: none;
      @include overlay();

      &:hover span {
        transform: translate(-50%, -50%) scale(1.1);
        -webkit-animation: pulseWhite 2s linear infinite;
        animation: pulseWhite 2s linear infinite;
      }
    }

    span {
      width: 60px;
      height: 60px;
      z-index: 4;
      position: absolute;
      color: rgb(var(--color-background));
      cursor: pointer;
      border: 5px solid transparent;
      @include transition_default();

      svg {
        z-index: 2;
        width: 40px;
        height: 40px;
        color: rgb(var(--color-foreground));
      }
    }

    iframe {
      z-index: 4;
      position: absolute;
      top: 0;
      left: 0;
      border: none;
      display: block;
      width: 100%;
      height: 100%;
      background: #000;
    }
  }
}

.media > .simpleParallax {
  position: relative;

  > img {
    display: block;
    max-width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center center;
  }
}

.page-hero__image {
  position: relative;

  .media {
    padding-bottom: 25%;

    @media screen and (max-width: 749px) {
      padding-bottom: 50%;
    }
  }
}

.page-title-overlay {
  position: absolute;
  bottom: 2rem;
  left: calc(12% + 5rem);
  color: white;

  @media screen and (max-width: 749px) {
    left: 1.5rem;
    bottom: 1.5rem;
  }

  nav {
    margin-bottom: 0;

    @media screen and (max-width: 749px) {
      font-size: 1rem;
    }
  }

  h2 {
    margin-top: 1rem;

    @media screen and (max-width: 749px) {
      margin-top: 0;
    }
  }
}

nav.sidebar__menu {

  ul {

    li {
      border-top: 1px solid rgb(var(--color-foreground));
      position: relative;
      padding-top: 2rem;
      padding-bottom: 2rem;

      @media screen and (max-width: 749px) {
        padding-top: 1rem;
        padding-bottom: 1rem;
        font-size: 1.2rem;
      }

      &:last-child {
        border-bottom: 1px solid rgb(var(--color-foreground));
      }

      a {
        text-decoration: none;
        display: block;

        &.menu--page-current {
          text-decoration: underline;
        }

        .icon--arrow {
          position: absolute;
          top: 50%;
          margin-top: -3px;
          right: 2px;
          @include transition_default();
        }

        &:hover {
          color: rgb(var(--color-foreground));

          .icon--arrow {
            right: -2px;
          }
        }
      }
    }
  }

  + div {
    margin-top: 5rem;
  }
}

.accordion details > summary .icon-caret {
  @include transition_default();
}

summary {

  &:focus {
    outline: none;
  }
}

tooltip-component {
  color: rgb(var(--color-button));

  .icon {
    line-height: 1rem;
    height: 14px;
    margin-left: 4px;
  }
}

.tooltip {
  font-size: rem(14px);
  line-height: rem(22px);

  .tooltip-inner {
    background: rgb(var(--color-foreground));
    color: rgb(var(--color-background));
    max-width: rem(300px);
    padding: 6px 8px;
  }
}



/************ ADJUSTMENTS ****************/

body {
  line-height: rem(24px);
}

body,
.color-background-1,
.color-background-2,
.color-inverted,
.color-accent-1,
.color-accent-2 {
  color: rgb(var(--color-foreground));
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  color: rgb(var(--color-foreground))
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5 {
  font-weight: 800;
}

.h6 {
  font-weight: 600;
}

h1,
.h1 {
  font-size: rem(58px);
  line-height: rem(63px);
  letter-spacing: -1px;

  @include media-query($max,$md) {
    font-size: rem(41px);
    line-height: rem(45px);
    letter-spacing: 0;
  }
}

h2,
.h2 {
  font-size: rem(48px);
  line-height: rem(53px);

  @include media-query($max,$md) {
    font-size: rem(28px);
    line-height: rem(34px);
  }
}

h3,
.h3 {
  font-size: rem(38px);
  line-height: rem(43px);

  @include media-query($max,$md) {
    font-size: rem(25px);
    line-height: rem(28px);
  }
}

h4,
.h4 {
  font-size: rem(28px);
  line-height: rem(33px);

  @include media-query($max,$md) {
    font-size: rem(22px);
    line-height: rem(26px);
  }
}

h5,
.h5 {
  font-size: rem(23px);
  line-height: rem(28px);

  @include media-query($max,$md) {
    font-size: rem(20px);
    line-height: rem(24px);
  }
}

h6,
.h6 {
  font-size: rem(18px);
  line-height: rem(24px);
}

p {
  margin-top: rem(24rem);
  margin-bottom: rem(24rem);
}

a {

  &:hover {
    text-decoration: none;
  }
}

.subtitle {
  font-size: 1.8rem;
  line-height: calc(1 + 0.8 / var(--font-body-scale));
  letter-spacing: 0.06rem;
  color: rgb(var(--color-foreground));
}

.color-heading {
  padding: 2rem 2.5rem;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 3.5rem;
  line-height: 1.2rem;

  @include media-query($max,$lg) {
    font-size: rem(25px);
    padding: rem(20px);
  }
}

.rte {

  a {
    font-weight: 800;
    color: var(--color-base-outline-button-labels);
  }

  ul {
    padding-left: 3rem !important;
  }
}

img {
  max-width: 100%;
}

blockquote {
  font-weight: 300;
  font-size: rem(60px);
  line-height: rem(60px);
  letter-spacing: -0.4px;
  font-style: normal;
  border-left: none;
  padding-left: 0;
  color: rgb(var(--color-foreground));

  cite {
    display: block;
    font-size: rem(20px);
    line-height: rem(25px);
    font-weight: 800;
    font-style: normal;
    margin-top: rem(20px);
  }
}

.button,
.button--primary,
.button--secondary,
.button--tertiary {
  max-height: none;
  padding: rem(9px) rem(24px);
  font-size: rem(18px);
  line-height: rem(24px);
  min-height: auto;
  font-weight: 600;
}

.button:before,
.shopify-challenge__button:before,
.customer button:before,
.shopify-payment-button__button--unbranded:before,
.shopify-payment-button [role="button"]:before,
.cart__dynamic-checkout-buttons [role='button']:before {
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
}

.button,
.copy-links .button,
.vidoverlay .banner__buttons .button {
  color: var(--buttons-primary-text);
  background-color: var(--buttons-primary-background);

  &::after {
    --button-shadow-color: var(--buttons-primary-border);
    box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
    0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    @include transition_default();
  }

  &:hover {
    color: var(--buttons-primary-text-hover);
    background-color: var(--buttons-primary-background-hover);

    &::after {
      --button-shadow-color: var(--buttons-primary-border-hover);
      box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
      0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    }
  }

  &.disabled,
  &:disabled,
  &.disabled:hover,
  &:disabled:hover {
    color: var(--buttons-primary-disabled-text);
    background-color: var(--buttons-primary-disabled-background);

    &::after {
      --button-shadow-color: var(--buttons-primary-disabled-border);
      box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
      0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    }
  }

  &.button--secondary {
    color: var(--buttons-secondary-text);
    background-color: var(--buttons-secondary-background);

    &::after {
      --button-shadow-color: var(--buttons-secondary-border);
      box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
      0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    }

    &:hover {
      color: var(--buttons-secondary-text-hover);
      background-color: var(--buttons-secondary-background-hover);

      &::after {
        --button-shadow-color: var(--buttons-secondary-border-hover);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
      }
    }

    &.button--secondary-rectangle {
      color: rgb(var(--color-foreground));

      &::after {
        border-radius: 0;
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) rgba(var(--color-foreground), var(--border-opacity)), 0 0 0 var(--buttons-border-width) rgba(var(--color-foreground), var(--alpha-button-background));
      }

      &:hover {
        background-color: transparent;
        color: rgb(var(--color-button-text));

        &::after {
          border-radius: 0;
          box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) rgba(var(--color-button-text), var(--border-opacity)), 0 0 0 var(--buttons-border-width) rgba(var(--color-button-text), var(--alpha-button-background));
        }
      }
    }

    &.disabled,
    &:disabled,
    &.disabled:hover,
    &:disabled:hover {
      color: var(--buttons-secondary-disabled-text);
      background-color: var(--buttons-secondary-disabled-background);

      &::after {
        --button-shadow-color: var(--buttons-secondary-disabled-border);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
      }
    }
  }

  &.button--tertiary {
    color: var(--buttons-tertiary-text);
    background-color: var(--buttons-tertiary-background);

    &::after {
      --button-shadow-color: var(--buttons-tertiary-border);
      box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
      0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    }

    &:hover {
      color: var(--buttons-tertiary-text-hover);
      background-color: var(--buttons-tertiary-background-hover);

      &::after {
        --button-shadow-color: var(--buttons-tertiary-border-hover);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
      }
    }

    &.disabled,
    &:disabled,
    &.disabled:hover,
    &:disabled:hover {
      color: var(--buttons-tertiary-disabled-text);
      background-color: var(--buttons-tertiary-disabled-background);

      &::after {
        --button-shadow-color: var(--buttons-tertiary-disabled-border);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
      }
    }
  }

  &.button--inverted {
    color: var(--buttons-inverted-text);
    background-color: var(--buttons-inverted-background);

    &::after {
      --button-shadow-color: var(--buttons-inverted-border);
      box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
      0 0 0 var(--buttons-border-width) var(--button-shadow-color);
    }

    &:hover {
      color: var(--buttons-inverted-text-hover);
      background-color: var(--buttons-inverted-background-hover);

      &::after {
        --button-shadow-color: var(--buttons-inverted-border-hover);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
      }
    }

    &.disabled,
    &:disabled,
    &.disabled:hover,
    &:disabled:hover {
      color: var(--buttons-inverted-disabled-text);
      background-color: var(--buttons-inverted-disabled-background);

      &::after {
        --button-shadow-color: var(--buttons-inverted-disabled-border);
        box-shadow: 0 0 0 calc(var(--buttons-border-width) + var(--border-offset)) var(--button-shadow-color),
        0 0 0 var(--buttons-border-width) var(--button-shadow-color);
