.button--text_link {
  padding: 0;
  color: rgb(var(--color-button));
  font-size: rem(14px);
  font-weight: 600;
  background: none;

  &::before,
  &::after {
    display: none;
  }
}

.subheading {
  color: var(--neutral-granite);
}

.hide {
  display: none !important;
}

.page-fullwidth {
  padding-left: rem(20px);
  padding-right: rem(20px);

  @include media-query($max,$md) {
    padding-left: rem(13px);
    padding-right: rem(13px);
  }
}

.page-width__padding {
  padding-left: rem(80px);
  padding-right: rem(80px);

  @include media-query($max,$md) {
    padding-left: rem(13px);
    padding-right: rem(13px);
  }
}

.page-height__padding {
  padding-top: rem(80px);

  @include media-query($max,$lg) {
    padding-top: rem(30px);
  }
}

.accordion__details {

  .icon {
    @include transition_default();
  }

  summary {

    .icon-plus,
    .icon-minus {
      position: absolute;
      height: calc(var(--font-heading-scale) * 1rem);
      right: 1.5rem;
      top: calc(50% - 0.4rem);
      opacity: 1;
    }

    .icon-minus {
      opacity: 0;
      height: auto;
      width: calc(var(--font-heading-scale) * 1rem);
    }
  }

  &[open] {

    .icon-plus {
      opacity: 0;
    }

    .icon-minus {
      opacity: 1;
    }
  }

  a {
    position: relative;
    text-decoration: none;
    display: flex;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
    color: rgb(var(--color-foreground));

    .icon-arrow {
      position: absolute;
      height: calc(var(--font-heading-scale) * 1rem);
      right: 1.5rem;
      top: calc(50% - 0.2rem);
      transform: rotate(90deg);
    }
  }
}

.accordion {
  border-color: $grey50;

  .product__description {
    margin-top: 0;
  }

  .rte {
    font-size: rem(14px);
    line-height: rem(22px);

    &.product__description {
      max-width: rem(545px);
    }

    #size-table {
      display: block;
      margin-top: rem(14px);
      margin-bottom: rem(14px);

      table {
        font-size: rem(10px);
        display: block;
        max-width: -moz-fit-content;
        max-width: fit-content;
        margin: 0 auto;
        overflow-x: auto;
        white-space: nowrap;

        tr {
          background: rgb(var(--color-background));
          @include transition_default();

          &:hover {
            background: $brand_lightblue;
          }
        }

        th {
          font-size: 600;
          background: $grey25;
        }

        th,
        td {
          padding: rem(6px);
        }
      }
    }
  }
}

.accordion .accordion__details summary {
  padding-top: rem(14px);
  padding-bottom: rem(14px);
}

.accordion .summary__title .accordion__title {
  font-size: rem(20px);
  line-height: rem(25px);
}

.header-wrapper--border-bottom {
  border-bottom: 1px solid $grey75;
}

.list-menu__item {
  font-size: rem(25px);
  line-height: rem(30px);
  font-weight: 800;
  color: rbg(var(--color-foreground));
  @include transition_default();

  .header__inline-menu .list-menu:hover & {

    &:not(:hover) {
      opacity: 1;
    }
  }

  &:hover {
    color: rgb(var(--color-button));

    span {
      text-decoration: none;
    }
  }

  details[open] > & {
    color: rgb(var(--color-button));
  }
}

summary.list-menu__item,
.header__menu-item {
  padding-right: rem(26px);
  padding-left: rem(26px);

  .mega-menu & {
  padding-right: rem(26px);

    svg {
      display: none;
    }
  }
}

.header__icon.header__icon--menu {
  height: auto;
  color: rgb(var(--color-button));

  .icon {
    width: 3rem;
    height: 3rem;
  }
}

.header__icon:hover .icon {
  transform: none;
}

.header__icon.header__icon--cart {
  margin-right: -8rem;
  justify-content: flex-end;
  overflow: hidden;
  width: auto;
}

.header__icon--cart,
.header__search {
  margin-top: -8px;
  margin-bottom: -8px;
}

.cart-count-bubble {
  background-color: rgb(var(--color-button-text));
  color: rgb(var(--color-button));
  left: 70%;
  top: 25%;
}

.breadcrumbs {

  .breadcrumbs__item {
    color: $brand_red;
    font-weight: 800;
    font-size: rem(18px);
    line-height: rem(24px);

    a {
      text-decoration: none;
      color: $brand_red;
    }
  }
}

.banner {
  overflow: hidden;

  ::after {
    left: 0;
  }

  &.banner--mobile-bottom.banner--desktop-transparent .banner__content {

    @include media-query($max,$md) {

      .button.button--inverted {
        color: var(--buttons-primary-border);

        &::after {
          --button-shadow-color: var(--buttons-primary-border);
        }
      }
    }
  }
}

.product-grid,
.grid.blog__posts {
  row-gap: rem(20px);
  column-gap: rem(20px);

  @include media-query($max,$md) {
    row-gap: rem(13px);
    column-gap: rem(13px);
  }

  &.grid--4-col-desktop > .grid__item {
    width: calc(25% - rem(20px) * 3 / 4);
    max-width: calc(25% - rem(20px) * 3 / 4);
  }

  &.grid--6-col-widemonitor > .grid__item {
    @media screen and (min-width: 2000px) {
      width: calc(16.66% - rem(20px) * 5 / 6);
      max-width: calc(16.66% - rem(20px) * 5 / 6);
    }
  }

  &.grid--2-col-tablet-down > .grid__item {
    @include media-query($max,$lg) {
      width: calc(50% - rem(20px) / 2);
      max-width: calc(50% - rem(20px) / 2);
    }

    @include media-query($max,$md) {
      width: calc(50% - rem(13px) / 2);
      max-width: calc(50% - rem(13px) / 2);
    }
  }
}

.grid.blog__posts {

  &.grid--4-col-desktop > .grid__item {
    width: calc(25% - rem(20px) * 3 / 4);
    max-width: calc(25% - rem(20px) * 3 / 4);
  }

  &.grid--3-col-desktop > .grid__item {
    width: calc(25% - rem(20px) * 2 / 3);
    max-width: calc(25% - rem(20px) * 2 / 3);
  }

  &.grid--2-col-desktop > .grid__item {
    width: calc(25% - rem(20px) * 1 / 2);
    max-width: calc(25% - rem(20px) * 1 / 2);
  }
}

.two-columns-main.two-columns-main__sidebyside {
  margin-bottom: 0;
}

.three-columns-inner.copy_block .copy-main {
  padding-top: 16%;
  padding-bottom: 16%;

  .copy-details {

    > *:last-child {
      margin-bottom: 0;
    }
  }
}

.two-columns-main.two-columns-main__sidebyside {
  gap: 0;
}

.page-width--narrow {
  max-width: rem(600px);
}

.sidebar-menu {
  border-right: 1px solid $grey75;

  h2 {
    margin: 0;
    font-size: rem(30px);
    line-height: rem(35px);
    padding: rem(30px);
  }

  ul {
    margin: 0;
    padding: 0;
    border-top: 1px solid $grey75;

    li {
      border-bottom: 1px solid $grey75;
      padding: 0;

      a {
        text-decoration: none;
        padding: rem(20px) rem(30px);
        font-size: rem(18px);
        line-height: rem(24px);
        font-weight: 800;
        background: transparent;
        @include transition_default();

        &.sidebar-menu__menu-item--active {
          background: rgb(var(--color-foreground));
          color: rgb(var(--color-background));
        }
      }

      &:hover a:not(.sidebar-menu__menu-item--active) {
        background: $brand_lightblue;
        color: rgb(var(--color-foreground));
      }
    }
  }
}

.field.field--heading,
.field.field--checkbox {

  &::before,
  &::after {
    display: none;
  }
}

.field {

  &.field--heading {

    h5 {
      margin-top: 1rem;
      margin-bottom: 0;
    }
  }

  &.field--checkbox {

    input:not(:placeholder-shown) {

      &:focus {
        outline: none;
        box-shadow: none;
      }

      + label {
        left: 25px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 1.6rem;
        line-height: 1.4rem;

        &::before,
        &::after {
          content: '';
          display: block;
          position: absolute;
          width: 15px;
          height: 15px;
          left: -25px;
          top: 50%;
          transform: translateY(-50%);
          border: 1px solid rgb(var(--color-foreground));
          z-index: 2;
        }

        &:after {
          opacity: 0;
          background: rgba(var(--color-button), var(--alpha-button-background));
          border: 3px solid rgb(var(--color-background));
          z-index: 1;
        }
      }

      &:checked + label {

        &::after {
          opacity: 1;
        }
      }
    }
  }
}

.thanks {

  &:not(.hidden) + * {
    display: none;
  }
}

.image-container {

  > img {
    width: 100%;
  }
}

.video-container {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;

  > iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.banner.banner--mobile-bottom.banner--desktop-transparent .banner__content-slider.banner__content,
.banner__content-slider.banner__content {
  height: 100%;

  &.banner__content--left-top {
    align-items: flex-start;
    justify-content: flex-start;
  }

  &.banner__content--left-center {
    align-items: center;
    justify-content: flex-start;
  }

  &.banner__content--left-bottom {
    align-items: flex-end;
    justify-content: flex-start;
  }

  &.banner__content--center-top {
    align-items: flex-start;
    justify-content: center;
  }

  &.banner__content--center-bottom {
    align-items: flex-end;
    justify-content: center;
  }

  &.banner__content--right-top {
    align-items: flex-start;
    justify-content: flex-end;
  }

  &.banner__content--right-center {
    align-items: center;
    justify-content: flex-end;
  }

  &.banner__content--right-bottom {
    align-items: flex-end;
    justify-content: flex-end;
  }

  .banner__buttons {
    max-width: 100%;

    @include media-query($max,$md) {

      .button {
        width: 100%;
      }
    }
  }

  &.banner__content--text-left .banner__buttons {
    justify-content: flex-start;
  }

  &.banner__content--text-center .banner__buttons {
    justify-content: center;
  }

  &.banner__content--text-right .banner__buttons {
    justify-content: flex-end;
  }

  .banner__box {

    .banner__text {
      font-weight: normal;
    }

    @include media-query($min,$md) {
      max-width: 50%;
    }

    @include media-query($min,$lg) {
      max-width: 33%;
    }
  }
}

.mega-menu[open] .mega-menu__content {
  z-index: 1;
}
