@-webkit-keyframes opacity_show {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes opacity_show {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes pulseWhite {
  0%{
    box-shadow: 0 0 0 5px rgba(255,255,255,0.7);
  }
  40%{
    box-shadow: 0 0 0 25px rgba(255,255,255,0);
  }
  80%{
    box-shadow: 0 0 0 25px rgba(255,255,255,0);
  }
  100%{
    box-shadow: 0 0 5px rgba(255,255,255,0);
  }
}

@keyframes pulseWhite {
  0%{
    box-shadow: 0 0 0 5px rgba(255,255,255,0.7);
  }
  40%{
    box-shadow: 0 0 0 25px rgba(255,255,255,0);
  }
  80%{
    box-shadow: 0 0 0 25px rgba(255,255,255,0);
  }
  100%{
    box-shadow: 0 0 5px rgba(255,255,255,0);
  }
}

@-webkit-keyframes slideInRight {
    0% { right: -100%; }
    50% { right: -100%; }
    100% { right: 20px; }
}

@keyframes slideInRight {
    0% { right: -100%; }
    50% { right: -100%; }
    100% { right: 20px; }
}

@-webkit-keyframes slideInRightDelay {
    0% { right: -100%; }
    100% { right: 0; }
}

@keyframes slideInRightDelay {
    0% { right: -100%; }
    100% { right: 0; }
}

@-webkit-keyframes slideOutRight {
    0% { right: 20px;  }
    100% { right: -100%; }
}

@keyframes slideOutRight {
    0% { right: 20px;  }
    100% { right: -100%; }
}

@-webkit-keyframes slideOutRightDelay {
    0% { right: 0;  }
    50% { right: 0;  }
    100% { right: -100%; }
}

@keyframes slideOutRightDelay {
    0% { right: 0;  }
    50% { right: 0;  }
    100% { right: -100%; }
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}


@keyframes spin {
  0% { transform: translate(-50%, -50%) rotate(0deg) scale(1);}
  100% { transform: translate(-50%, -50%) rotate(1079deg) scale(1); }
}

@-webkit-keyframes spin {
  0% {-webkit-transform: translate(-50%,-50%) rotate(0deg) scale(1);}
  100% {-webkit-transform: translate(-50%,-50%) rotate(1079deg) scale(1);}
}

@keyframes success{
  from {
    transform: translate(-50%, -50%) rotate(0) scale(0);
  }
  to {
    transform: translate(-50%, -50%) rotate(-45deg) scale(1);
  }
}

@-webkit-keyframes success{
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0) scale(0);
  }
  to {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg) scale(1);
  }
}

@keyframes error {
  from {
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    background-color: #f44336;
  }
}

@-webkit-keyframes error {
  from {
    -webkit-transform: translate(-50%, -50%) scale(0);
  }
  to {
    -webkit-transform: translate(-50%, -50%) scale(1);
    background-color: #f44336;
  }
}


@keyframes vibrate {
  0%, 30%, 60%, 85%, 100% {
    left: 0;
    background-color: #f44336;
  }
  10%, 40%, 90%, 70% {
    left: -2px;
    background-color: #f44336;
  }
  20%, 50%, 80%, 95% {
    left: 2px;
    background-color: #f44336;
  }
}


@keyframes formshow {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}
